import back from '@/components/back'
import noData from '@/components/noData'
export default {
    components:{
        back,
        noData
    },
    data(){
        return{
            title: '选择药房', 
			list:[],
			chooseData:null,
			user: this.$store.state.userInfo,
        }
    },
    created(){
        this.pull()
    },
    mounted(){
		
    },
    beforeDestroy(){
        window.onscroll = null
    },
    methods: {
        pull(){
			this.$post('Prescription/Pharmacy/GetMyMedicationCompany',{ //获取我的供药单位
				DoctorId:this.user.Data.User.DoctorId,
				MedicationType:2,
				OrgId:this.user.Data.User.OrgId
			 }).then(o => {
				if(o){
					o.forEach(a => {
						if(a.IsDefault){
							a.state = 'success' 
							this.chooseData = a
						}else{
							a.state = 'circle' 
						}
					})
				}
				this.list = o
			 })
        },
		organizationChoose(i){
			if(i.state == "circle"){
				this.list.forEach(a => {
					a.state = 'circle' 
				})
				i.state = 'success'
			}
			this.chooseData = i
		},
        circleSate(){
			if(this.showStae == "circle"){
				this.showStae = "success-circle"
			}else{
				this.showStae = "circle"
			}
		},
		btnSave(){
			var c = 0;
			this.list.forEach(a => {
				if(a.state.indexOf("success") != -1){
					c = 1
				}
			})
			if(c==0){
				return this.$toast('请选择药房')
			}
			this.$post('Prescription/Pharmacy/SetMyMedicationCompany',{ 
				MedicationCompanyId:this.chooseData.Id,
				DoctorId:this.user.Data.User.DoctorId,
				MedicationType:2,
				OrgId:this.user.Data.User.OrgId
			 }).then(o => {
				this.$router.push('/chooseDrugs?cid=1')
			 })
			
		},
    }
}



